import { Component, OnInit } from '@angular/core';
import { ModalController, NavParams } from '@ionic/angular';

@Component({
  selector: 'app-country',
  templateUrl: './country.page.html',
  styleUrls: ['./country.page.scss'],
})
export class CountryPage implements OnInit {

  country: any;
  res: any;
  data: any;
  value: any;
  cname: any;
  selected: any;

  constructor(public modalctrl: ModalController,
    public navparam: NavParams) { 
      this.cname = this.navparam.get('country')
      this.selected = this.cname
    }

  ngOnInit() {
    fetch('./assets/country/country.json').then(res => res.json())
      .then(json => {
        this.country = json;
        console.log(this.country)
      });
  }

  search(ev: any) {
    this.data = this.country;
    console.log(this.data)
    let q = ev.target.value;
    console.log(q)
    if (q && q.trim() != '') {
      this.data = this.data.filter((result) => {
        this.res = result;
        return (result.name.toLowerCase().indexOf(q.toLowerCase()) > -1)
      })
    }
    // else {
    //   this.data = []
    // }
  }

  select(id) {
    this.value = id;
    var data = { id: id }
    console.log(data)
    // this.modalctrl.dismiss(data)
  }

  dimissok(){
    var data = { id: this.value }
    console.log(data)
    this.modalctrl.dismiss(data)
  }

  cancel(){
    this.modalctrl.dismiss()
  }
}
